.first-screen {
	.container-fluid {
		padding: 115px 0 41px 0;
		background-image: url(assets/img/first-screen.svg);
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center center;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.first-screen-img {
		font-family: "Lobster";
		font-style: normal;
		font-weight: 400;
		font-size: 240px;
		color: rgba(90, 71, 55, 0.1);

		letter-spacing: 0.04em;
		-webkit-text-stroke: 16px rgba(255, 255, 255, 0.2); /* для Safari и Chrome */

		text-shadow: rgba(90, 71, 55, 0.1);
	}
	.title-second {
		text-align: center;
	}
}
