.menu {
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;

	.img-bg-firs {
		position: absolute;
		left: 1200px;
		top: 170px;
		z-index: -1;
	}
	.img-bg-second {
		position: absolute;
		left: -30px;
		top: 570.21px;
		z-index: -1;
	}
	.img-bg-third {
		position: absolute;
		top: 1239px;
		right: -32px;
		z-index: -1;
	}
	.menu-fast {
		margin-left: 40px;
	}

	.menu-mainbox {
		display: flex;
		background-color: $bg-color;
		padding: 50px;
		border: 12px solid #6f5643;
		border-radius: 16px;

		&__template {
			border: 4px solid #6f5643;
			border-radius: 16px;
			padding: 15px;
			max-width: 437px;
			min-width: 437px;
		}
		&__template-name {
			font-family: "Lobster";
			font-style: normal;
			font-weight: 400;
			font-size: 36px;
			line-height: 45px;
			color: #6f5643;
			background-color: #e1b971;
			border-radius: 8px;
			text-align: center;
			margin-bottom: 34px;
			padding: 8px 0;
		}
		.breakfast {
			color: white;
			background-color: #6f5643;
		}
		&__template-item {
			margin-bottom: 16px;
			border-bottom: 2px solid #b19e90;
			.group-menu {
				font-family: "Lobster";
				font-style: normal;
				font-weight: 400;
				font-size: 24px;
				line-height: 30px;
				color: #6f5643;
				margin-bottom: 12px;
				display: flex;
				justify-content: space-between;
			}
			&-description {
				font-family: "Proza Libre";
				font-style: normal;
				font-weight: 400;
				font-size: 20px;
				line-height: 27px;
				color: #8a7768;
				margin-bottom: 12px;
			}
		}
		.coffe-menu {
			padding: 0 10px;
			text-align: center;
			margin-bottom: 23;
			position: relative;
			&:after {
				position: absolute;
				content: " ";
				top: 0;
				left: 50%;
				transform: translate(0, -40%) rotate(90deg);
				height: 240px;
				width: 2px;
				background-color: #e88360;
			}
		}
		.margen-menu {
			margin-top: 50px;
		}
	}
}
