.advantages {
	background-repeat: no-repeat;
	background-image: url(assets/img/bg.svg);
	background-position-y: 70%;
	max-width: 1440px;
	margin: 0 auto;
	.container {
		display: flex;
		flex-direction: column;
		align-items: center;
		.title-second {
			color: white;
		}
	}
	.advantages-content {
		background: url(assets/img/cakeadvantage.svg);
		background-position: center center;
		background-position-x: 100%;
		background-size: 100%;
		display: flex;
		background-repeat: no-repeat;
		margin-bottom: 45px;

		justify-content: center;
		min-height: 550px;
		padding: 200px 91px 20px;
		&__box {
			text-align: center;

			.template-discription {
				text-align: start;
				max-width: 324px;
				color: white;
			}
		}
		&__box:first-child {
			margin-right: 100px;
		}
		&__box:last-child {
			margin-left: 125px;
		}
	}
}
