.about {
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center center;
	background-image: url(assets/img/bg.svg);
	background-position-y: 100%;
	margin: 0 auto;
	padding-bottom: 79px;
	max-width: 1440px;

	.template-discription {
		max-width: 470px;
	}

	.container {
		position: relative;
	}

	.container-fluid {
		min-height: 800px;
		background-image: url(assets/img/bg.svg);
		background-position: center;
		background-repeat: no-repeat;
		background-size: contain;
		margin: 0 -36px;
		max-width: 80%;
	}
	.about-template {
		display: flex;
		justify-content: center;
		justify-content: baseline;
	}
	.about-box {
		margin-right: 59px;

		&__shadow {
			background-color: #6f5643;
			border-radius: 12px;
		}
		&__item {
			margin-top: 60px;
			margin-bottom: 92px;
			max-width: 648px;
			padding: 40px;
			border-radius: 12px;
			background-color: $bg-color;
			border: 4px solid #d2a24c;
			transform: translate(-8px, -8px);
		}
	}
	.btn-box {
		max-width: 341px;
		margin: 0 auto;
		.btn.btn-primary {
			max-width: 342px;
			white-space: nowrap;
			padding: 19.5px 85.5px;
		}
	}
}
