.slider {
	margin-top: 60px;
	display: flex;
	flex-direction: column;
	align-items: center;
	.main-box-first {
		display: flex;
		align-items: flex-end;
		margin-bottom: 90px;
		.about-box {
			&__shadow {
				background-color: #6f5643;
				border-radius: 12px;
			}
			&__item {
				margin-bottom: 86px;
				max-width: 648px;
				padding: 40px;
				border-radius: 12px;
				background-color: $bg-color;
				border: 4px solid #d2a24c;
				transform: translate(-8px, -8px);
			}
		}
	}
	.slider-template {
		max-width: 672px;
		margin-right: 32px;
	}
	.slider-template__item-wrapper {
		max-width: 672px;
		max-height: 590px;
	}
	.slider-first-img {
		max-width: 672px;
		max-height: 590px;
	}
	.slider-second-img {
		width: 280px;
		height: 187px;
		border-radius: 16px;
		border: 4px solid #6f5643;
	}
	.slider-template-second {
		max-width: 652px;
		position: relative;
		margin: 0 -25px;
	}
	.slider-template__item-wrapper-second {
		margin: 0 25px;
	}
	.slick-arrow {
		background-color: transparent;
		border: none;
		font-size: 0;
		width: 68px;
		height: 68px;
		top: 95px;
		left: -341px;
		position: absolute;

