header {
	background-color: $bg-color;
	.container {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	.logo {
		padding: 20px 0;
		border-bottom: 3px solid $primary-color;
		max-width: 1376px;
		width: 100%;
		text-align: center;
		.navbar-brand {
			font-family: "Lobster";
			font-style: normal;
			font-weight: 400;
			font-size: 32px;
			line-height: 40px;
			letter-spacing: 0.04em;
			color: $primary-color;
			position: relative;
			&:before {
				content: "";
				position: absolute;
				left: -55%;
				top: 50%;
				transform: translateY(-55%);
				width: 80px;
				height: 2px;
				background-color: $line-color;
			}
			&:after {
				content: "";
				position: absolute;
				left: 110%;
				top: 50%;
				transform: translateY(-55%);
				width: 80px;
				height: 2px;
				background-color: $line-color;
			}
		}
	}
	.navbar-nav {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 auto;
	}
	.nav-item:last-child {
		.nav-link {
			margin-right: 0;
		}
	}
	.nav-link {
		padding: 20px 0;
		font-weight: 400;
		font-size: 16px;
		font-family: "Playfair Display";
		font-style: normal;
		font-weight: 500;
		font-size: 24px;
		line-height: 32px;
		margin-right: 110px;

		color: $button-main-color;

		&:hover {
			color: $stroke-color;
			transition: 0.5s;
		}
		&:focus {
			color: $button-main-color;
			transition: 0.5s;
		}
		&:active {
			color: $primary-color;
			transition: 0.5s;
		}
	}

	.navbar {
		padding: 0;
		margin-top: 8px;
		max-width: 1376px;
		width: 100%;

		border: 2px solid $line-color;
		border-left: none;
		border-right: none;
		margin-bottom: 24px;
	}

	.header-logo {
		display: inline-block;
	}

	.navbar-toggler {
		width: 40px;
		height: 40px;
		position: relative;

		&:focus {
			box-shadow: none;
		}

		span,
		span:before,
		span:after {
			cursor: pointer;
			border-radius: 1px;
			height: 2px;
			width: 35px;
			background: white;
			position: absolute;
			left: 0;
			top: 50%;
			display: block;
			content: "";
		}

		span:before {
			top: -10px;
		}

		span:after {
			top: 10px;
		}

		span,
		span:before,
		span:after {
			transition: all 0.5s ease-in-out;
		}
		&[aria-expanded="true"] {
			span {
				background-color: transparent;
			}
			span:before,
			span:after {
				top: 0;
			}
			span:before {
				transform: rotate(135deg);
			}
			span:after {
				transform: rotate(-135deg);
			}
		}
	}
}
