footer {
	background-color: $element-color;
	padding: 45px 0 20px;
	.container {
		.navbar-nav-footer {
			padding: 0;
		}
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		.nav-item:last-child {
			.nav-link {
				margin-right: 0;
			}
		}
		.navbar-nav-footer {
			display: flex;
			justify-content: center;
			align-items: center;
			list-style: none;
			margin: 28px 0 25px;
			.nav-link {
				font-family: "Playfair Display";
				font-style: normal;
				font-weight: 500;
				font-size: 24px;
				line-height: 32px;
				margin-right: 45px;

				color: #ffffff;

				&:hover {
					color: #8a7768;
					transition: 0.5s;
				}
				&:focus {
					color: $button-main-color;
					transition: 0.5s;
				}
				&:active {
					color: #6f5643;
					transition: 0.5s;
				}
			}
		}
	}
	.navbar-nav-img {
		list-style: none;
		display: flex;
		align-items: center;

		.icon-footer:hover {
			transition: 0.4s;
			stroke: #ffffff;
			rect {
				fill: #8a776842;
			}

			path {
				fill: white;
			}
		}
		.icon-footer:active {
			transition: 0.4s;
			stroke: #ffffff;
			rect {
				fill: #6f56435a;
			}

			path {
				fill: white;
			}
		}
	}

	.navbar-brand {
		font-family: "Lobster";
		font-style: normal;
		font-weight: 400;
		font-size: 32px;
		line-height: 40px;
		/* identical to box height */
		letter-spacing: 0.04em;
		color: #ffffff;
		position: relative;
		&:before {
			content: "";
			position: absolute;
			left: -55%;
			top: 50%;
			transform: translateY(-55%);
			width: 80px;
			height: 2px;
			background-color: $stroke-color;
		}
		&:after {
			content: "";
			position: absolute;
			left: 110%;
			top: 50%;
			transform: translateY(-55%);
			width: 80px;
			height: 2px;
			background-color: $stroke-color;
		}
	}
}
