			background-image: url("data:image/svg+xml,%3Csvg width='240' height='52' viewBox='0 0 240 52' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M35 11V15.5M47 11V15.5M28.25 21.635H53.75M27.5 27.515V20.75C27.5 16.25 29.75 13.25 35 13.25H47C52.25 13.25 54.5 16.25 54.5 20.75V33.5C54.5 38 52.25 41 47 41H35C29.75 41 27.5 38 27.5 33.5' stroke='%238A7768' stroke-width='2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M46.541 28.55H46.5545M46.541 33.05H46.5545M40.9925 28.55H41.006M40.9925 33.05H41.006M35.4425 28.55H35.4575M35.4425 33.05H35.4575' stroke='%238A7768' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M212.48 22L204.873 29.6067C203.975 30.505 202.505 30.505 201.607 29.6067L194 22' stroke='%238A7768' stroke-width='2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3Crect x='1' y='1' width='238' height='50' rx='11' stroke='%238A7768' stroke-width='2'/%3E%3C/svg%3E");
