		.col-md-9 {
			padding: 0;
			display: flex;
			margin-bottom: 50px;
			label {
				margin-right: 30px;
				margin-top: 6px;
			}
			.dropdown-center {
				.btn {
					border: 2px solid #8a7768;
					background-color: transparent;
					border-radius: 16px;
					min-width: 240px;
					height: 52px;
					text-align: center;
					position: relative;
					font-family: "Proza Libre";
					font-style: normal;
					font-weight: 400;
					font-size: 24px;
					line-height: 33px;
					color: #8a7768;

					&::after {
						display: inline-block;
						position: absolute;
						top: 5px;
						left: 22px;
						margin-left: 0;
						vertical-align: 0;
						content: "";
						border-top: 0;
						border-right: 0;
						border-bottom: 0;
						border-left: 0;
						background-image: url("data:image/svg+xml,%3Csvg width='36' height='36' viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M22.53 4.51498C21.2284 3.5242 19.6358 2.99155 18 2.99998C13.86 2.99998 10.5 6.35998 10.5 10.5C10.5 14.64 13.86 18 18 18C22.14 18 25.5 14.64 25.5 10.5M30.885 33C30.885 27.195 25.11 22.5 18 22.5C10.89 22.5 5.11499 27.195 5.11499 33' stroke='%238A7768' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
						width: 36px;
						height: 36px;
					}
					&::before {
						position: absolute;
						display: inline-block;
						left: 186px;
						top: 11px;
						content: "";
						border-top: 0;
						border-right: 0;
						border-bottom: 0;
						border-left: 0;
						width: 28px;
						height: 28px;
						background-image: url("data:image/svg+xml,%3Csvg width='28' height='28' viewBox='0 0 28 28' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M23.24 10.4417L15.6333 18.0483C14.735 18.9467 13.265 18.9467 12.3667 18.0483L4.76001 10.4417' stroke='%238A7768' stroke-width='2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
					}
					&:hover {
						&::before {
							background-image: url("data:image/svg+xml,%3Csvg width='22' height='11' viewBox='0 0 22 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.75999 9.55835L9.36666 1.95168C10.265 1.05335 11.735 1.05335 12.6333 1.95168L20.24 9.55835' stroke='%23E1B871' stroke-width='2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
							background-repeat: no-repeat;
							top: 19px;
							left: 189px;
							transition: 0.2s;
						}
					}
					&:focus {
						box-shadow: none;
						&::before {
							background-image: url("data:image/svg+xml,%3Csvg width='22' height='11' viewBox='0 0 22 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.75999 9.55835L9.36666 1.95168C10.265 1.05335 11.735 1.05335 12.6333 1.95168L20.24 9.55835' stroke='%23E1B871' stroke-width='2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
							background-repeat: no-repeat;
							top: 19px;
							left: 189px;
							transition: 0.2s;
						}
					}
				}
				.dropdown-menu {
					background-color: #faf4d7;
					width: 128px;
					border: 2px solid #8a7768;
					border-radius: 12px;
					margin-top: 15px;
					padding: 10px 10px;

					.dropdown-item {
						color: #8a7768;
						font-weight: 400;
						font-size: 24px;
						line-height: 33px;
						text-align: center;
						margin-bottom: 16px;
						border-bottom: 2px solid #8a7768;

						&:hover {
							color: #d2a24c;
							border-bottom: 2px solid #d2a24c;
							background-color: #faf4d7;
							transition: 0.3s;
						}
						&:active {
							color: #6f5643;
							border-bottom: 2px solid #6f5643;
							transition: 0.3s;
						}
					}
				}
			}
		}
	}
	.img-contacts {
		position: absolute;
		top: 916px;
		left: 1160px;
	}
}
