.history {
	.template-history {
		display: flex;

		justify-content: space-between;

		.img-display-box {
			display: flex;
			margin-bottom: 380px;
			img:last-child {
				margin: 0;
				transform: translate(0, 171px);
			}
		}
		.template-discription {
			max-width: 470px;
			margin-bottom: 250px;
		}
		.img-box-first {
			margin-right: 0;
			transform: translate(198px, 0);
		}
		&__box {
			margin-top: 160px;
			display: flex;
			flex-direction: column;
			img {
				margin: 0 19px 20px 0;
			}
		}

		.template-history__box-img-second-down {
			max-width: 542px;
			display: flex;
			flex-wrap: wrap;
			.img-second {
				margin: 0;
				transform: translate(0, 171px);
			}
		}
		.template-history__box-second {
			background-image: url("data:image/svg+xml,%3Csvg width='203' height='2358' viewBox='0 0 203 2358' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 4.58253C0 1.02949 4.28674 -0.760044 6.81287 1.73852L98.201 92.1293C99.754 93.6654 102.252 93.6715 103.813 92.1431L196.201 1.6587C198.733 -0.820763 203 0.972852 203 4.51643V2353.39C203 2356.95 198.706 2358.74 196.182 2356.23L103.832 2264.56C102.267 2263.01 99.7395 2263.02 98.182 2264.58L6.83189 2356.15C4.31345 2358.68 0 2356.89 0 2353.33V4.58253Z' fill='%23E1B971'/%3E%3C/svg%3E");
			min-height: 2;
			max-height: 100%;
			padding: 192px 24px 229px;
			margin: 0 40px;
			height: 2358px;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
		}
		&__box-group {
			font-family: "Lobster";
			font-style: normal;
			font-weight: 400;
			font-size: 96px;
			line-height: 120px;

			color: #ffffff;
			text-align: center;
		}
	}
}
